<template>
    <div :class="{isSelf: isSelf}">
        <!---->
        <div v-if="messageInfo.msg_type === 'text'" class="message-box text">{{ messageInfo.msg_content }}</div>
        <!---->
        <template v-else-if="messageInfo.msg_type === 'image' || messageInfo.msg_type === 'emotion'">
            <a-tooltip v-if="messageInfo.file_is_remove" title="">
                <img src="@/assets/image/session/load-img-deleted.png" style="width: 120px;"/>
            </a-tooltip>
            <div v-else-if="messageInfo.msg_content " class="message-box image pointer">
                <a-image :src="messageInfo.msg_content" style="max-width: 200px;"/>
            </div>
            <a-tooltip v-else title="">
                <img src="@/assets/image/session/load-img-run.png" style="width: 120px;"/>
            </a-tooltip>
        </template>
        <!---->
        <template v-else-if="messageInfo.msg_type === 'voice'">
            <div @click="playingVoice(messageInfo)" class="message-box voice zm-pointer">
<!--                <a-popover placement="right" overlayClassName="zm-tooltip-popover">-->
<!--                    <template #content>-->
<!--                        <div class="pointer" @click="voice2text(message)"></div>-->
<!--                    </template>-->
                    <div>
                        <span> [] </span>
                        <img style="width: 16px;" src="@/assets/image/icon-voice.png"/>
                        <span>{{ formatSeconds(messageInfo.raw_content.play_length) }}</span>
                    </div>
<!--                </a-popover>-->
            </div>
<!--            <div v-if="messageInfo.vioce2textLoading || messageInfo.vioce_text">-->
<!--                <div v-if="messageInfo.vioce2textLoading"-->
<!--                     class="voice-text-show"-->
<!--                     style="text-align: center;">-->
<!--                    <a-spin size="small"></a-spin>-->
<!--                </div>-->
<!--                <div v-else class="voice-text-show">{{ messageInfo.vioce_text }}</div>-->
<!--            </div>-->
        </template>
        <!--  -->
        <div v-else-if="messageInfo.msg_type === 'link'" class="message-box">
            <span v-if="messageInfo.hasOwnProperty('link_url')">
                []<a target="_blank" :href="messageInfo.link_url">{{ messageInfo.link_title }}</a>
            </span>
            <span v-else>
                []<a target="_blank" :href="messageInfo.link">{{ messageInfo.link }}</a>
            </span>
        </div>
        <!--  -->
        <div v-else-if="messageInfo.msg_type == 'video'" class="message-box">[]</div>
        <!--  -->
        <div v-else-if="messageInfo.msg_type == 'file'"
             :class="['message-box file-box', {warning: downloadFileLimit(messageInfo.content)}]">
            <img class="file-icon" :src="getFileIcon(messageInfo.raw_content.fileext)"/>
            <div class="file-info-box">
                <div class="left-block">
                    <a-tooltip
                        :title="messageInfo.raw_content.filename.length > 20 ? messageInfo.raw_content.filename : ''">
                        <div class="file-name">{{ messageInfo.raw_content.filename || '--' }}</div>
                    </a-tooltip>
                    <div class="file-size">{{ showFileSize(messageInfo.raw_content.filesize) }}</div>
                </div>
                <div class="right-block">
                    <a-tooltip v-if="messageInfo.file_is_remove" title="">
                        <DownloadOutlined style="color: #CCC;"/>
                    </a-tooltip>
<!--                    <a-tooltip v-else-if="remaining_size <= 0" :title="getTotalStorageSizeTitle()">-->
<!--                        <DownloadOutlined style="color: #FB363F;" @click="showBuyFileStorage()"/>-->
<!--                    </a-tooltip>-->
<!--                    <a-tooltip v-else-if="downloadFileLimit(messageInfo.msg_content)" title="100M">-->
<!--                        <DownloadOutlined style="color: #FB363F;"/>-->
<!--                    </a-tooltip>-->
                    <a v-else-if="messageInfo.msg_content" @click="downloadMsgFile(messageInfo)">
                        <DownloadOutlined/>
                    </a>
                    <a-tooltip v-else title="">
                        <DownloadOutlined style="color: #CCC;"/>
                    </a-tooltip>
                </div>
            </div>
        </div>
        <!--  -->
        <div v-else-if="messageInfo.msg_type == 'voip_doc_share'" class="message-box">[]</div>
        <!--  -->
        <div v-else-if="messageInfo.msg_type == 'meeting_voice_call'"
             class="message-box pointer"
             @click="playingVoice(messageInfo.msg_content, messageInfo.play_length)">
            []
            <img style="width: 16px;" src="@/assets/image/icon-voice.png"/>
            <span>{{ formatSeconds(messageInfo.play_length) }}</span>
        </div>
        <!--  -->
        <div v-else class="message-box">[{{ MessageTypeTextMap[messageInfo.msg_type] }}]</div>
        <span v-if="messageInfo.is_revoke" class="message-box" style="color: rgba(0,0,0,.25);"></span>
    </div>
</template>

<script setup>
import {ref} from 'vue';
import {Modal, message} from 'ant-design-vue';
import {DownloadOutlined} from '@ant-design/icons-vue';
import {downloadFile, formatBytes, formatSeconds, getFileIcon, MessageTypeTextMap} from "@/utils/tools";
import BenzAMRRecorder from 'benz-amr-recorder';

const props = defineProps({
    messageInfo: {
        type: Object,
        default: () => {
            return {}
        }
    },
    isSelf: {
        type: Boolean,
        default: false
    }
})

const currentAmrMsgId = ref(null)
const amrPlayer = ref(null)
const totalStorage = ref(10)
const voice2text = (messageInfo) => {
    messageInfo.vioce2textLoading = true
    // voice2text({
    //     crop_int_id: this.corpInterId,
    //     voice_url: messageInfo.content.content,
    // }).then(res => {
    //     messageInfo.vioce_text = res.data || ""
    // }).finally(() => {
    //     messageInfo.vioce2textLoading = false
    // })
}

const playingVoice = (msg) => {
    if (!msg.msg_content) {
        return;
    }
    try {
        if (currentAmrMsgId.value === msg.msg_id && amrPlayer.value) {
            amrPlayer.value.stop()
            currentAmrMsgId.value = null
            amrPlayer.value = null
            return
        }
        currentAmrMsgId.value = msg.msg_id
        amrPlayer.value = new BenzAMRRecorder();
        amrPlayer.value.initWithUrl(msg.msg_content).then(res => {
            amrPlayer.value.play()
        })

    } catch (err) {
        console.error(':', err);
        message.error('')
    }
}

const stopPlayVoice = () => {

}

const getTotalStorageSizeTitle = () => {
    return "" + totalStorage + "G"
}

const downloadMsgFile = msg => {
    downloadFile(msg.msg_content, msg.raw_content.filename)
}

const downloadFileLimit = fileCont => {

}

const showFileSize = size => {
    if (!size) {
        return ''
    }
    return formatBytes(size)
}

const showBuyFileStorage = () => {
    Modal.confirm({
        title: ``,
        okText: ``,
        cancelText: ' ',
        width: '500px',
        centered: true,
    })
}
</script>

<style scoped lang="less">
.isSelf {
    .message-box {
        background: #D4E3FC;
        text-align: left;
        border: none;
    }

    .message-box:after {
        content: "";
        position: absolute;
        left: 100%;
        top: 6px;
        border-top: 3px solid transparent;
        border-left: 6px solid #D4E3FC;
        border-bottom: 3px solid transparent;
    }
}

.message-box {
    display: inline-block;
    background: #FFFFFF;
    padding: 4px 8px;
    max-width: 40vw;
    white-space: normal;
    word-break: break-all;
    border-radius: 2px;
    opacity: 1;
    border: 1px solid #e6e6e6;
    position: relative;
    font-size: 12px;
    font-weight: 400;
    color: #595959;

    &.warning {
        border: 1px solid #FB363F;
        background: linear-gradient(0deg, #FFEBEC 0%, #FFEBEC 100%), #FFF;
    }

    &::after {
        content: "";
        display: block;
        width: 7px;
        height: 8px;
        position: absolute;
        top: 6px;
        left: -7px;
        background: url(@/assets/image/icon-arrow-left.png) 0 0 no-repeat;
    }

    &.text {
        display: inline-block;
    }

    &.voice {
        div {
            display: flex;
            align-items: center;
        }
    }

    &.file-box {
        display: flex;
        align-items: center;
        color: #262626;
        font-size: 14px;
        font-weight: 400;
        max-width: 300px;

        .file-icon {
            flex-shrink: 0;
            width: 40px;
            height: 40px;
            margin-right: 8px;
        }

        .file-info-box {
            flex-shrink: 0;
            width: calc(100% - 48px);
            display: flex;
            align-items: center;
            justify-content: space-between;

            .left-block {
                flex-shrink: 0;
                width: calc(100% - 20px);

                .file-name {
                    word-break: break-all;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                }

                .file-size {
                    color: #8c8c8c;
                    font-size: 12px;
                    margin-top: 2px;
                }
            }

            .right-block {
                flex-shrink: 0;
                width: 20px;
                font-size: 16px;
                color: #8C8C8C;
                text-align: right;
            }
        }
    }
}
</style>
